@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');

html {
  scroll-behavior: smooth;
}

.Container {
  position: relative;
}

.Home {
  color: lightgray;
  font-family: 'Roboto Condensed', sans-serif;
  position: absolute;
  background-color: transparent;
}

.gradientBackground {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(rgb(83, 159, 231), purple);
  overflow: hidden;
  position: fixed;
  z-index: -1;
}

.overlayLanguageSwitch {
  background-color: transparent;
  overflow: hidden;
  position: fixed;
  z-index: 1;
  right: 0;
}

.Home-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Home-logo {
    animation: Home-logo-spin infinite 20s linear;
  }
}

.Home-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Home-link {
  color: #61dafb;
}

@keyframes Home-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.ProjectSliderSection {
  display: flex;
  justify-content: center;
}

.IntroductionText {
  width: 100%;
}